body {
  font-family: "Noto Serif JP", serif;
  font-weight: 400;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.colorbox {
  position: relative;
  width: 80%;
  margin: 30px auto 30px;
}

.colorbox div.color {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #10404c;
}

.colorbox img {
  display: block;
  width: 100%;
}